export const initLayoutShift = (entries: Array<PerformanceEntryEncapsulation>) => {
  const lastEntry = entries.pop()
  if (lastEntry && !lastEntry.hadRecentInput && lastEntry.value) {
    cls.value += lastEntry.value
  }
}